.bgk{
  background-image: linear-gradient(-20deg, #616161 0%, #9bc5c3 100%);
}

.hero {
    height: 300px;
    background-size: cover;
    background-position: center;
    position: relative;
  }

  .hero::after {
    content: "";
    display: block;
    opacity: .4;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

  }
  
  .hero * {
    position: relative;
    z-index: 2;
  }
  
  .hero h1, .hero h2 {
    color: white;
  }
  
  .hero h1 {
    font-size: 60px;
    padding-top: 140px;
    margin-top: 0;
  }