body {
  background-image: -webkit-linear-gradient(30deg, #29323c 0%, #485563 100%);
  background-image: linear-gradient(60deg, #29323c 0%, #485563 100%);
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.picHolder{
    float: left;
    margin-left: 5px;
    margin-right: 5px;
    margin-bottom:15px;
    
}

.card-img-top{
    box-shadow: 1px 3px 29px 4px rgba(64,78,166,1);
    position: relative;
    left: 2%;
    top:4%;
    -webkit-transition: all 0.37s ease;
    transition: all 0.37s ease;
}

.card-img-top:hover{
    box-shadow: 1px 3px 29px 4px rgb(206, 41, 41);


}
.bgk{
  background-image: -webkit-linear-gradient(110deg, #616161 0%, #9bc5c3 100%);
  background-image: linear-gradient(-20deg, #616161 0%, #9bc5c3 100%);
}

.hero {
    height: 300px;
    background-size: cover;
    background-position: center;
    position: relative;
  }

  .hero::after {
    content: "";
    display: block;
    opacity: .4;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

  }
  
  .hero * {
    position: relative;
    z-index: 2;
  }
  
  .hero h1, .hero h2 {
    color: white;
  }
  
  .hero h1 {
    font-size: 60px;
    padding-top: 140px;
    margin-top: 0;
  }
.far{
    position: relative;
    left:100%;
    
}

.full{
    width:100%; 
}

.item-a{
    float: left;
    margin: 5px;

}

.navbar{
    position: fixed;
    z-index:99;
    width:100%;
    box-shadow:1px 3px 32px 4px rgba(122,122,122,1);
    background-image: -webkit-linear-gradient(bottom, #1e3c72 0%, #1e3c72 1%, #2a5298 100%);
    background-image: linear-gradient(to top, #1e3c72 0%, #1e3c72 1%, #2a5298 100%);
}
.footer {
    position: relative;
    left: 0;
    bottom: 0;
    width: 100%;
    padding-top: 2rem;
    padding-bottom: 2rem;
    background-image: -webkit-linear-gradient(bottom, #1e3c72 0%, #1e3c72 1%, #2a5298 100%);
    background-image: linear-gradient(to top, #1e3c72 0%, #1e3c72 1%, #2a5298 100%);
    color: white;
    text-align: center;
  }
.wrong{

    -webkit-animation: Wrong 0.5s ease;

            animation: Wrong 0.5s ease;
}

@-webkit-keyframes Wrong{
    0%{
        -webkit-transform: translate(-10px);
                transform: translate(-10px);
    }
    20%{
        -webkit-transform: translate(10px);
                transform: translate(10px);
    }
    40%{
        -webkit-transform: translate(-10px);
                transform: translate(-10px);
    }
    60%{
        -webkit-transform: translate(10px);
                transform: translate(10px);
    }
    80%{
        -webkit-transform: translate(-10px);
                transform: translate(-10px);
    }
    100%{
        -webkit-transform: translate(10px);
                transform: translate(10px);
    }

}

@keyframes Wrong{
    0%{
        -webkit-transform: translate(-10px);
                transform: translate(-10px);
    }
    20%{
        -webkit-transform: translate(10px);
                transform: translate(10px);
    }
    40%{
        -webkit-transform: translate(-10px);
                transform: translate(-10px);
    }
    60%{
        -webkit-transform: translate(10px);
                transform: translate(10px);
    }
    80%{
        -webkit-transform: translate(-10px);
                transform: translate(-10px);
    }
    100%{
        -webkit-transform: translate(10px);
                transform: translate(10px);
    }

}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

